.battle {
  padding: 0 0.32rem;

  .battleTitle {
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
    width: 100%;
    text-align: center;
    font-weight: bold;
  }

  .battleStep {
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -moz-box-pack: center;
     -ms-flex-pack: center;
         justify-content: center;
    width: 100%;

    .battleStepItem {
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      -moz-box-orient: vertical;
      -moz-box-direction: normal;
       -ms-flex-direction: column;
           flex-direction: column;
      -moz-box-align: center;
       -ms-flex-align: center;
           align-items: center;

      .stepItemName {
        font-size: 0.2rem;
        margin: 0.08rem 0.16rem;
      }

      .stepItemIcon {
        font-size: 0.6rem;
        width: 100%;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -moz-box-align: center;
         -ms-flex-align: center;
             align-items: center;
        -moz-box-pack: center;
         -ms-flex-pack: center;
             justify-content: center;
        background-color: #eeeeee;
        padding: 0.16rem;
      }

      .stepItemIconUser {
        @extend .stepItemIcon;
        color: #f5c27f;
      }

      .stepItemIconBattle {
        @extend .stepItemIcon;
        color: red;
      }

      .stepItemIconWinner {
        @extend .stepItemIcon;
        color: #f2ea57;
      }
    }
  }

  .battlePlayer {
    margin-top: 0.1rem;

    .battlePlayerTitle {
      width: 100%;
      text-align: center;
      font-weight: bold;
    }

    .battlePlayerContext {
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      -moz-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;

      .battlePlayerContextItem {
        margin-top: 0.1rem;
        width: 48%;

        .contextItemTitle {
          font-weight: bold;
          margin-bottom: 0.1rem;
        }

        .contextItemData {
          display: -moz-box;
          display: -ms-flexbox;
          display: flex;
          -moz-box-align: center;
           -ms-flex-align: center;
               align-items: center;

          .ant-btn {
            height: auto;
          }

          .contextItemDataInput {
            margin-right: 0.1rem;
          }
        }
      }

      .renderPlayer {
        margin-top: 0.1rem;
        width: 48%;
        padding: 0.16rem;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -moz-box-align: center;
         -ms-flex-align: center;
             align-items: center;
        -moz-box-pack: justify;
         -ms-flex-pack: justify;
             justify-content: space-between;
        background-color: #dfdfdf;
        border-radius: 0.04rem;

        .renderPlayerData {
          display: -moz-box;
          display: -ms-flexbox;
          display: flex;
          -moz-box-align: center;
           -ms-flex-align: center;
               align-items: center;

          img {
            border-radius: 0.08rem;
            width: 0.36rem;
            height: 0.36rem;
            margin-right: 0.08rem;
          }
          .itemImage{
            border-radius: 0.08rem;
            width: 0.36rem;
            height: 0.36rem;
            margin-right: 0.08rem;
          }
          .playerName {
            font-size: 0.2rem;
            font-weight: bold;
            color: #1890ff;
          }
        }

        .renderPlayerIcon {
          font-size: 0.18rem;
          color: red;
          cursor: pointer;
        }
      }
    }

    .battleButton {
      margin-top: 0.1rem;
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      -moz-box-align: center;
       -ms-flex-align: center;
           align-items: center;
      -moz-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;

      .ant-btn {
        height: auto;
      }
    }
  }
}